@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-color: #D5ECEA !important;
}
.body {
    background-color: #D5ECEA !important;
    
}

.card {
    margin-bottom: 4rem;
    margin-top: 1rem;
    min-width: 5rem;
}

.card img{
    max-height: 20rem;
}


.card:hover{
    box-shadow: 5px 10px 20px 1px rgba(0,0,0,0.253) !important
}

.card-body{
    padding: 1rem 0 !important;
}

.card-text{
    font-size: .9rem;
    padding: 0.4rem 1.9 rem; 
}

.container-fluid{
    padding-top: 5%;
    border-top: 10px solid #D5ECEA;
    border: 30px solid #D5ECEA;
    background-color: #D5ECEA;
}

.overflow {
    overflow: hidden;
}

.card-img-top{
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-transition: -webkit-transform 0.5s ease;
    transition: -webkit-transform 0.5s ease;
    transition: transform 0.5s ease;
    transition: transform 0.5s ease, -webkit-transform 0.5s ease;
}

.card-img-top:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}


div {
  background-color: white;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #0C3B55;
  font-family: DINCondensed-Bold;
  text-transform: uppercase;
  display: flex;
  justify-content: center;
}

li {
  float: left;
  
}

li a {
  display: block;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  color: #ffffff;
}

li a:hover{
    text-decoration: none;
    color: #ffffff;
}

li a:after{
	width:0%;
	content: "";
  margin:auto;
  display:inline-block;
  float: left;
  margin-top:-3px;
	border-top:3px solid #F57722;
	-webkit-transition: all .25s;
            transition: all .25s;	
}


li a:hover:after{
	width:100%;
}

li a:active, li a:active:after{
	border-color:#ccc;
	color:#F57722;

}


span {
  margin: 5px;
}

nav {
  height: 52px;
}

img {
  max-width: 7rem;
}

.titleblurb {
  margin: 7rem;
}
.logo {
    margin: 1%;
}
h1 {
    text-transform: uppercase;
    font-family: "DINCONDENSED-BOLD";
    text-align: center;
}
.button {
    margin: 5px;
}
.library {
    text-align: center;
}

.blurb {

    max-width: 50%;
    font-family: "Open Sans";
    display: inline-block;
    font-size: 12px;
}
